<template>
    <div class="editor-container">
        <p class="expain">vue2-editor<a href="https://www.awesomes.cn/repo/davidroyer/vue2-editor" target="_blank"></a></p>
        <vue-editor id='editor' useCustomImageHandler @imageAdded="handleImageAdded" v-model="content"></vue-editor>
    </div>
</template>
<script>
import { VueEditor } from 'vue2-editor'
import {upload} from '@/api/api.js'
export default {
    components: {
      VueEditor
   },
    data() {
       return {
         content: '<h1></h1>'  
       }
     },
     methods:{
         /** 
          * @param {Object} file 
          * @param {Object} Editor 
         */
         handleImageAdded(file, Editor, cursorLocation){
            let formData=new FormData();
            formData.append('image',file)
            //
            upload(formData).then(res=>{
                let  url = res.data.url 
                Editor.insertEmbed(cursorLocation, 'image', url);
            }).catch(e=>{

            })
         }
     }
}
</script>

